import {
  CART_ADD_ITEM,
  CART_REMOVE_ITEM,
  CART_SAVE_SHIPPING_ADDRESS,
  CART_SAVE_PAYMENT_METHOD,
  CART_RESET
} from "../constants/cartConstants";

export const cartReducer = (state = { cartItems: [] ,shippingAddress:{}}, action) => {
  switch (action.type) {
    case CART_ADD_ITEM:
      const item = action.payload;

      const existItem = state.cartItems.find((cartItem) => {
        return cartItem.product === item.product;
      });
      if (existItem) {
        return {
          ...state,
          cartItems: state.cartItems.map((x) =>
            x.product === existItem.product ? item : x
          ),
        };
      } else {
        return { ...state, cartItems: [...state.cartItems, item] };
      }

    case CART_REMOVE_ITEM:
      const { payload } = action;
      const modifiedCartItems = state.cartItems.filter(
        (cartItem) => cartItem.product !== payload
      );
      return { ...state, cartItems: modifiedCartItems };

    case CART_SAVE_SHIPPING_ADDRESS:
      return { ...state, shippingAddress: action.payload };

    case CART_SAVE_PAYMENT_METHOD:
        return { ...state, paymentMethod: action.payload };

    case CART_RESET:
      return {...state,cartItems:[]};

    default:
      return state;
  }
};
